<template>
    <div class="products-view" v-if="products && categoryInfo && generalContent">
        <page-title :title="categoryInfo.name[lang]"/>
        <div class="wrapper">
            <div class="products-view-head">
                <div class="cat-search-field">
                    <input type="text" :placeholder="generalContent.headerSearchFieldText[lang]" v-model="query">
                </div>
                <div class="actions hidden-xs visible-sm">
                    <button class="action" @click="productsListType = 'list'" :class="{active: productsListType === 'list'}">
                        <span class="fas fa-list"></span>
                    </button>
                    <button class="action" @click="productsListType = 'grid'" :class="{active: productsListType === 'grid'}">
                        <span class="fas fa-grip-vertical"></span>
                    </button>
                </div>
            </div>
            <div class="products-section">
                <div class="filters-section" :class="{active: isFilterWindowOpen}">
                    <app-button class="close-btn" icon="fas fa-times" @click="closeFilterWindow()" />
                    <div class="applied-filters" v-if="selectedFiltersInfo.length">
                        <h3 class="title">Примененные фильтры</h3>
                        <div class="list">
                            <div class="item" v-for="(selectedFilter, i) in selectedFiltersInfo" :key="i">
                                <span class="value">{{ selectedFilter.name[lang] }}</span>
                                <button class="close" @click="filterTrigger(selectedFilter)">
                                    <span class="fas fa-times"></span>
                                </button>
                            </div>
                        </div>
                    </div>
                    <div class="filters-list">
                        <h4 class="title">Фильтры</h4>
                        <div class="list">
                            <div class="filter-box active" v-for="(option, i) in categoryInfo.list" :key="i">
                                <div class="filter-box-head" @click="toggleFilterBox">
                                    <div class="fbh-title">{{ option.name[lang] }}</div>
                                    <button class="collapse">
                                        <span class="fas fa-chevron-down"></span>
                                    </button>
                                </div>
                                <div class="filter-box-body">
                                    <div class="filter" v-for="(filter, j) in option.list" :key="j">
                                        <input type="checkbox" :id="'f' + filter.id" @input="filterTrigger(filter)">
                                        <label :for="'f' + filter.id">{{ filter.name[lang] }}</label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="products-list">
                    <div class="filter-mobile-button">
                        <app-button class="block" text="Фильтры" icon="fas fa-filter" @click="openFilterWindow()"/>
                    </div>
                    <template v-if="filteredProducts.length">
                        <div class="flex-row-12" v-if="productsListType === 'grid'">
                            <div class="flex-col xs-12 sm-6" v-for="(product, i) in filteredProducts" :key="i">
                                <product-card :product="product"/>
                            </div>
                        </div>
                        <div class="flex-row-12" v-if="productsListType === 'list'">
                            <div class="flex-col xs-12" v-for="(product, i) in filteredProducts" :key="i">
                                <product-card-inline :product="product"/>
                            </div>
                        </div>
                    </template>
                    <template v-else>
                        <div class="msg">
                            <span class="fas fa-sad-tear"></span>
                            <p>{{ pageText.emptyCatalogMsg[lang] }}</p>
                        </div>
                    </template>
                </div>
            </div>
        </div>
        <questions-form />
    </div>
</template>

<script>
import AppButton from '../components/AppButton.vue';
import QuestionsForm from '../components/QuestionsForm.vue';
import PageTitle from '../components/PageTitle.vue';
import ProductCard from '../components/ProductCard.vue';
import ProductCardInline from '../components/ProductCardInline.vue';

export default {
    data() {
        return {
            query: '',
            pageText: {
                emptyCatalogMsg: {
                    ua: 'Вибачте, але ми не можемо знайти товари по Вашому запиту',
                    ru: 'Извините, но мы не можем найти товары по Вашему запросу',
                    en: 'Sorry, but we cant find any products by your request'
                }
            },
            selectedFiltersId: [],
            selectedFiltersInfo: [],
            isFilterWindowOpen: false,
            productsListType: 'grid'
        }
    },  
    components: {
        QuestionsForm,
        PageTitle,
        ProductCard,
        AppButton,
        ProductCardInline
    },
    computed: {
        lang() {
            return this.$store.getters['getSelectedLang'];
        },
        categoryInfo() {
            return this.$store.getters['getCategoryById'](this.$route.params.id);
        },
        products() {
            return this.$store.getters['getProductsByCategoryId'](this.$route.params.id);
        },
        generalContent() {
            return this.$store.getters['getGeneralPageContent'];
        },
        filteredProducts() {
            return (this.selectedFiltersId.length || this.query.length)
                ? this.products.filter(product => {
                        return product.options.some((option) => {
                                return (this.selectedFiltersId.indexOf(option) != (-1));
                            }) 
                            &&
                            (~product.name['ua'].toLowerCase().indexOf(this.query.toLowerCase())
                            || ~product.name['ru'].toLowerCase().indexOf(this.query.toLowerCase())
                            || ~product.name['en'].toLowerCase().indexOf(this.query.toLowerCase()))
                    })
                : this.products;
        }
    },
    methods: {
        openFilterWindow() {
            this.isFilterWindowOpen = true;
        },
        closeFilterWindow() {
            this.isFilterWindowOpen = false;
        },
        toggleFilterBox(event) {
            event.target.closest('.filter-box').classList.toggle('active');
        },
        filterTrigger(filter) {
            let elementIndex = this.selectedFiltersId.indexOf(filter.id);
            if (elementIndex != (-1)) {
                this.selectedFiltersInfo.splice(elementIndex, 1);
                this.selectedFiltersId.splice(elementIndex, 1);
                document.querySelector('.filter-box input#f' + filter.id).checked = false;
            } else {
                this.selectedFiltersInfo.push(filter);
                this.selectedFiltersId.push(filter.id);
            }
        }
    }
}
</script>